import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { EducationTemplate } from './education-template/education-template';

@Injectable({
  providedIn: 'root'
})
export class EducationService {
  private educationUrl = '/assets/education.json';
  constructor(private httpClient: HttpClient) { }

  getEducation(): Observable<EducationTemplate[]> {
    return this.httpClient.get<EducationTemplate[]>(this.educationUrl);
  }
}
