import { Component, OnInit } from '@angular/core';
import { EducationTemplate } from './education-template/education-template';
import { EducationService } from './education.service';

@Component({
  selector: 'app-education',
  templateUrl: './education.component.html',
  styleUrls: ['./education.component.css']
})
export class EducationComponent implements OnInit {

  public schools: EducationTemplate[];
  constructor(private experienceService: EducationService) { }

  ngOnInit() {
    this.experienceService.getEducation()
      .subscribe(
        schools => this.schools = schools,
        error => console.log(error)
      );
  }


}
